import { Injectable } from '@angular/core';
import { HttpClient } from "@angular/common/http";
import {path  } from "../properties";
@Injectable({
  providedIn: 'root',
})
export class SoftwareDownloadsExistService {
  private bluepageAPI : string;

  constructor(private http:HttpClient) { 
    this.bluepageAPI = "https://bluepages.ibm.com/BpHttpApisv3/slaphapi?ibmperson/";
  }

  
    getCountry(){
        return this.http.get<any>(path+"/getCountry")
    
       // return this.http.get<any>("http://9.120.240.199:3000/getRowData/"+id)
    }

    getData(id){
      return this.http.get<any>(path+"/getSDtrans/"+id)
    }

  updateForm(data){
      console.log((data))
     
      return this.http.post<any>(path+"/updateSDtrans",data)
    //   return this.http.post<any>("http://9.120.240.199:3000/saveTransaction",data)
  }

  getExcel(id){
    return this.http.get<any>(path+"/getExcelDataSD/"+id)
  }

  getUsersFromBluepage(data: string) {
    return this.http.get(this.bluepageAPI+"(|(mail=*"+data+"*)(cn=*"+data+"	*)).list,40/byJSON?mail&uid&notesemail&jobresponsibilities&cn&givenname")
    .toPromise()
     
  }
  getLicense(){
    return this.http.get<any>(path+"/getLicense")

   // return this.http.get<any>("http://9.120.240.199:3000/getRowData/"+id)
}

}

