submit(){
 this.submitted = true;
  console.log(this.sdformExist.value)
  var body={};
  body=this.sdformExist.value;
 body['Custom_Question_Xls_File']=(this.filesBase64);
console.log(body);
  if(this.sdformExist.invalid  && this.fileflag){
    return;
  }
  this.isLoading=true;
  this.overlay=true;
  this.sdService.updateForm(body)
  .subscribe(
    data=>{
    console.log("success",data)
  if(data.statusCode==200){
    this.isLoading=false;
  this.overlay=false;
    localStorage.setItem('transactionId',data.transactionId);
    localStorage.setItem('content',"Thanks for the Update");
    this.router.navigateByUrl('/home-four');
  }
  },
    error=>{console.log("errror",error)
    this.isLoading=false;
    this.overlay=false;
  }
  )
}




searchUsers(event: any) {
  event.preventDefault();
  event.stopPropagation();
  if ((event.keyCode >= 37 && event.keyCode <= 40) || event.keyCode == 13 || event.keyCode == 16) {

  } else {
    this.user = event.target.value;
    if (event.target.value !==null && event.target.value.length > 2) {
      this.sdService.getUsersFromBluepage(event.target.value).then(data => {
        this.searchedUsers = this.generateUserData(data);
        console.log("already")
        console.log(this.searchedUsers);
  
      //  this.isSearchBoxOpen = false;
      },
      error=>{console.log(error);
      });
    } else {
      this.user = '';
      this.searchedUsers = "";
      //this.isSearchBoxOpen = true;
      //this.setUserData.emit('');
    }
  }
}

generateUserData(obj: any) {
  var result = [];
  obj['search']['entry'].forEach((element, index) => {
    let tmp = {};
    element['attribute'].forEach(elem => {
      if(elem.name=="mail"){
        tmp['content']=elem.value[0];
       // alert()
      }
      else
      tmp[elem.name] = elem.value[0];
    });
    result.push(tmp);
  });
  return result;
}




