import { Injectable } from '@angular/core';
import { HttpClient } from "@angular/common/http";
import { path } from "../properties";
@Injectable({
  providedIn: 'root',
})
export class MrsaasService {

  private bluepageAPI : string;

  constructor(private http:HttpClient) {
    
    this.bluepageAPI = "http://bluepages.ibm.com/BpHttpApisv3/slaphapi?ibmperson/";
   }


  sendForm(data){
      console.log((data))
     
      return this.http.post<any>(path+"/saveTransaction",data)
    //   return this.http.post<any>("http://9.120.240.199:3000/saveTransaction",data)
  }

  getTemplate(){
    return this.http.get<any>(path+"/templates/CQ")
  }
  getUsersFromBluepage(data: string) {
    return this.http.get(this.bluepageAPI+"(|(mail=*"+data+"*)(cn=*"+data+"	*)).list,40/byJSON?mail&uid&notesemail&jobresponsibilities&cn&givenname")
    .toPromise()
     
  }

}