import { Component, OnInit,ChangeDetectorRef } from "@angular/core";
import {timer} from 'rxjs';
import { trigger, state, style, animate, transition } from '@angular/animations';

@Component({
	selector: "app-home",
	templateUrl: "./home.component.html",
  styleUrls: ["./home.component.scss"],
  animations: [
    trigger('fade', [
      transition('void => *', [
        style({ opacity: 0.2 }),
        animate(5000, style({opacity: 1}))
      ])
    ])
  ]
})
export class HomeComponent implements OnInit {
  imgs;
  idd;
  currentState;
  h;
  screenHeight;
  constructor(private cdr: ChangeDetectorRef) {

   }

  ngOnInit() {
    this.reSize();
    this.idd = 0;
    timer(500, 10000).subscribe( x => {
       this.alertFunc();
       });
  }

  reSize() {
    this.h = window.innerHeight - (document.getElementById('footerId').offsetHeight + document.getElementById('top').offsetHeight +document.getElementById('headerId').offsetHeight);
    this.screenHeight = (this.h) + 'px';
    document.getElementById('main1').style.minHeight = this.screenHeight;
    // <HTMLElement>document.getElementsByClassName('box')[0]. = this.screenHeight;
    (<HTMLInputElement>document.getElementsByClassName('box')[0]).style.height = this.screenHeight;
  }


// changeState() {
//   this.currentState = this.currentState === 'initial' ? 'final' : 'initial';
// }

alertFunc() {
  if (this.idd < 3) {
    this.idd++;
  } else {
    this.idd = 1;
  }
  // this.changeState();
 // console.log(this.id);
}
 demo() {
 }

}
