import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { HomeComponent } from "./home/home.component";
import { HomeTwoComponent } from "./home-two/home-two.component";
import { HomeThreeComponent } from "./home-three/home-three.component";
import { HomeFourComponent } from "./home-four/home-four.component";
import { MrsaasNewComponent } from "./mrsaas-new/mrsaas-new.component";
import { MrsaasExistingComponent } from "./mrsaas-existing/mrsaas-existing.component";
import {SoftwareDownloadsNewComponent} from './software-downloads-new/software-downloads-new.component'
import { SoftwareDownloadsExistComponent } from "./software-downloads-exist/software-downloads-exist.component";

const routes: Routes = [
	{ path: "", redirectTo: "home", pathMatch: "full" },
	{ path: "home", component: HomeComponent },
	{ path: "home-two", component: HomeTwoComponent },
	{ path: "home-three", component: HomeThreeComponent },
	{ path: "home-four", component: HomeFourComponent },
	{ path: "mrsaas/new", component: MrsaasNewComponent },
	{ path: "mrsaas/existing", component:MrsaasExistingComponent },
	{path:"software-downloads/new",component:SoftwareDownloadsNewComponent},
	{path:"software-downloads/exist",component:SoftwareDownloadsExistComponent}
];

@NgModule({
	imports: [RouterModule.forRoot(routes,{useHash:true})],
	exports: [RouterModule]
})
export class AppRoutingModule {}
