import { Injectable } from '@angular/core';
import { HttpClient } from "@angular/common/http";
import {path  } from "../properties";
@Injectable({
  providedIn: 'root',
})
export class MrsaasExistingService {
  private bluepageAPI : string;

  constructor(private http:HttpClient) {
    
    this.bluepageAPI = "http://bluepages.ibm.com/BpHttpApisv3/slaphapi?ibmperson/";
   }

    getData(id){
        return this.http.get<any>(path+"/getRowData/"+id)

       // return this.http.get<any>("http://9.120.240.199:3000/getRowData/"+id)
    }

  sendForm(data){
      console.log((data))

      return this.http.post<any>(path+"/updateTransaction",data)
    //   return this.http.post<any>("http://9.120.240.199:3000/saveTransaction",data)
  }
  getExcel(id){
    return this.http.get<any>(path+"/getExcelData/"+id)
  }
  
  getUsersFromBluepage(data: string) {
    return this.http.get(this.bluepageAPI+"(|(mail=*"+data+"*)(cn=*"+data+"	*)).list,40/byJSON?mail&uid&notesemail&jobresponsibilities&cn&givenname")
    .toPromise()
     
  }


}

